import OverlayPopup from '../../pages/Booth/OverlayPopup';

interface Props {
  onClose: (e: boolean) => void;
}

export default function BannerPopup(props: Props) {
  const { onClose } = props;
  return (
    <OverlayPopup onClose={onClose}>
      <div className="flex w-full">
        <img
          src="/img/banners/banner-1.png"
          className="ml-2 w-full rounded-lg object-cover"
          alt="banner"
        />
      </div>
    </OverlayPopup>
  );
}
